.navbar {
  box-shadow: 0 12px 20px rgba(0, 0, 0, 0.1);
}
.logo {
  width: 100%;
  height: 50px;
  background: transparent;
}
.navbar-light .navbar-nav .nav-link.active,
.navbar-light .navbar-nav .show > .nav-link {
  background: -webkit-linear-gradient(rgba(0, 224, 254), rgba(17, 7, 254));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
section {
  padding-top: 80px;
  padding-bottom: 80px;
  z-index: 2;
}
.bg-cover {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  background-attachment: fixed;
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(17, 7, 254, 0.7), rgba(0, 224, 254, 0.7));
  z-index: -1;
}
video {
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.hero-section {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn {
  padding: 14px 22px;
  transition: all 0.3s ease;
}
.btn-main {
  background: linear-gradient(to right, rgba(0, 224, 254), rgba(17, 7, 254));
  border-radius: 100px;
  border: none;
  font-weight: 600;
  color: #fff;
}
.btn:hover {
  color: #fff;
  transform: translateY(-5px);
}
.video-btn {
  width: 60px;
  height: 60px;
  background: linear-gradient(to right, rgba(0, 224, 254), rgba(17, 7, 254));
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  border-radius: 100px;
}
.video-btn i {
  font-size: 32px;
}
.section-intro {
  margin-bottom: 40px;
}
.section-intro .divider {
  height: 4px;
  width: 70px;
  background: linear-gradient(to right, rgba(0, 224, 254), rgba(17, 7, 254));
  margin: 16px auto;
}
img {
  width: 100%;
}
.service .service-img {
  position: relative;
}
.service .service-img img {
  height: 250px;
}
.service .service-img .icon {
  width: 90px;
  height: 90px;
  background: linear-gradient(to right, rgba(0, 224, 254), rgba(17, 7, 254));
  color: #fff;
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: -45px;
  left: 50%;
  transform: translateX(-50%);
}
.service .service-img .icon i {
  font-size: 36px;
}
.feature i {
  background: -webkit-linear-gradient(rgba(0, 224, 254), rgba(17, 7, 254));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 54px;
}
.review {
  box-shadow: 0 12px 12px rgba(0, 0, 0, 0.1);
  min-height: 420px;
}
.review .person {
  display: flex;
  align-items: center;
  padding-bottom: 18px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.07);
}
.review .person img {
  width: 75px;
  height: 75px;
  border-radius: 100px;
  object-fit: cover;
}
.review .stars {
  color: orange;
}
form .form-control {
  border-radius: 0;
  background: transparent;
  border-color: rgb(255, 255, 255, 0.5);
}
form input.form-control {
  height: 54px;
}
form .form-control::placeholder {
  color: #fff;
}
form .form-control:focus {
  color: #fff;
  background: transparent;
  border-radius: 0;
  border-color: #fff;
}
footer {
  background: linear-gradient(to right, rgba(0, 224, 254), rgba(17, 7, 254));
  color: #fff;
}
footer a {
  color: #fff;
}
.text-justify {
  text-align: justify;
}
